.CreateTodoButton {
    background-color: #e9e9e9cd;
    box-shadow: 0px 5px 25px rgba(68, 69, 69, 0.34);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #545454ae;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
}

.modal-opened {
    transform: rotate(224deg);
}